.kui-theme--dark {
  --row-selected-transparent: rgba(11, 53, 75, 0); }

.pipeline-row__toggle:before {
  position: absolute;
  top: 0;
  bottom: 0;
  left: -100px;
  display: block;
  width: 100px;
  background: transparent;
  background: linear-gradient(90deg, var(--row-selected-transparent, 0) 0%, var(--color-nodelist-row-selected) 100%);
